const style = on => {
  return {
    "background-color": on ? "grey" : "white",
    border: "1px solid black",
    margin: "1rem"
  };
};

const steps = step => {
  const steps = [...Array(8)];
  return steps.map((_, i) => {
    return (
      <div className="column" style={style(step == i + 1 ? true : false)} />
    );
  });
};

function click(state) {}

const Track = props => (
  <div className="columns" onClick={click(props.state)}>
    {steps(props.step)}
  </div>
);

export default Track;
